// URL()
let hotSearchUrl = '/ebapi/store_api/hot_search';
const baseUrl = '/common'
const files = '/files'

// vmthisvmvuexuView
// https://uviewui.com/js/http.html#%E4%BD%95%E8%B0%93%E8%AF%B7%E6%B1%82%E6%8B%A6%E6%88%AA%EF%BC%9F
const install = (Vue, vm) => {
	// params
	let getSearch = (params = {}) => vm.$u.get(hotSearchUrl, {
		id: 2
	});

	// 
	const login = (params) => vm.$u.post(`${baseUrl}/login`, params)
	
	// upload
	const upload = (params) => vm.$u.post(`${files}/upload`, params)

	// vm.$u.api(vmthisthis.$u.api)
	vm.$u.api = {
		getSearch,
		login
	};
}

export default {
	install
}
