/**
 * 
 * @return {isNotEmpty} 
 * @return {isId} 
 * @return {isPhone} 
 * @return {isEmail} 
 * @return {isAdult} 
 * @return {isIndate} 
 * @return {isChinese} 
 * @return {isAddress} 
 * @return {pwdStrength} 
 */

export default {
	/**
	 * uViewtrim
	 *  
	 * @param {String} str 
	 */
	isNotEmpty: (str) => {
		if (!str.trim().length) return {
			flag: false,
			label: ''
		}
		if (str.trim() !== str) return {
			flag: false,
			label: ''
		}
		return { flag: true }
	},
	/**
	 * 
	 * @param {String} id
	 */
	isId: function(id) {
		const verify = this.isNotEmpty(id)
		if (!verify.flag) return {
			flag: false,
			label: `${verify.label}`
		}
		const city = {
			11: '',
			12: '',
			13: '',
			14: '',
			15: '',
			21: '',
			22: '',
			23: ' ',
			31: '',
			32: '',
			33: '',
			34: '',
			35: '',
			36: '',
			37: '',
			41: '',
			42: ' ',
			43: '',
			44: '',
			45: '',
			46: '',
			50: '',
			51: '',
			52: '',
			53: '',
			54: ' ',
			61: '',
			62: '',
			63: '',
			64: '',
			65: '',
			71: '',
			81: '',
			82: '',
			91: ''
		}
		const birth = id.substr(6, 4) + '/' + Number(id.substr(10, 2)) + '/' + Number(id.substr(12, 2))
		// const birthdayFormat = birth.replace(/\//g, '-0000').replace(/-[\d]+([\d]{2})/g, '-$1') // new YYYY-MM-DD
		// const birthdayFormat = birth.replace(/\//g, '-0000').replace(/-[\d]+([\d]{2})/g, '+$1') // new YYYY-MM-DD
		const birthday = birth.replace(/\//g, '++')
		const d = new Date(birth)
		const currentTime = new Date().getTime()
		const time = d.getTime()
		const arrInt = [7, 9, 10, 5, 8, 4, 2, 1, 6, 3, 7, 9, 10, 5, 8, 4, 2] // 
		const arrCh = ['1', '0', 'X', '9', '8', '7', '6', '5', '4', '3', '2'] // 
		const arrCh2 = ['1', '0', 'x', '9', '8', '7', '6', '5', '4', '3', '2'] // 
		let sum = 0
		const sex = (Number(id.substr(16, 1)) % 2) === 0 ? 1 : 0
		const sexName = ['', ''][sex] // 0.1.
		if (!/^\d{17}(\d|x|X)$/.test(id)) return {
			flag: false,
			label: ''
		}
		if (city[id.substr(0, 2)] === undefined) return {
			flag: false,
			label: ''
		}
		if (time >= currentTime) return {
			flag: false,
			label: ''
		}
		for (let i = 0; i < 17; i++) {
			sum += id.substr(i, 1) * arrInt[i]
		}
		const residue = arrCh[sum % 11]
		const residue2 = arrCh2[sum % 11]
		if (residue === id.substr(17, 1) || residue2 === id.substr(17, 1)) {
			return {
				flag: true,
				sex: sex + '',
				sexName,
				birth,
				birthday
			}
		}
		return {
			flag: false,
			label: ''
		}
	},
	/**
	 * 
	 * @param {String} phone
	 */
	isPhone: function(phone) {
		if (!this.isNotEmpty(phone)) return {
			flag: false,
			label: ''
		}
		const reg = /^[1][3,4,5,7,8][0-9]{9}$/
		if (!reg.test(phone)) return {
			flag: false,
			label: ''
		}
		return {
			flag: true
		}
	},
	/**
	 * 
	 * @param {String} email
	 */
	isEmail: function(email) {
		if (!this.isNotEmpty(email)) return {
			flag: false,
			label: ''
		}
		const regx = /^([a-zA-Z\d])((\w|-)+\.?)+@([a-zA-Z\d]+\.)+[a-zA-Z]{2,6}$/
		if (!regx.test(email)) return {
			flag: false,
			label: ''
		}
		return {
			flag: true
		}
	},
	/**
	 *  
	 * @param {DateStr} birth 
	 */
	isAdult: function(birth) {
		// 
		if (!this.isNotEmpty(birth)) return {
			flag: false,
			label: ''
		}
		const birthday = new Date(birth.replace(/-/g, '/'))
		// const birthday = new Date(birth)
		const today = new Date()
		const age = today.getFullYear() - birthday.getFullYear() - ((today.getMonth() < birthday.getMonth() || (today.getMonth() ===
			birthday.getMonth() && today.getDate() < birthday.getDate())) ? 1 : 0)
		if (age < 18) return {
			flag: false,
			label: ''
		}
		return {
			flag: true
		}
	},
	/**
	 *  
	 * @param {DateStr} date 
	 */
	isIndate: function(date) {
		if (!date) return {
			flag: false,
			label: ''
		}
		if (new Date(date.replace(/-/g, '/')) < new Date()) return {
			flag: false,
			label: ''
		}
		return {
			flag: true
		}
	},
	/**
	 * 
	 * @param {String} type english chinese engChinese
	 * @param {String} str 
	 */
	isChinese: function(str, type = 'chinese') {
		if (!this.isNotEmpty(str)) return {
			flag: false,
			label: ''
		}
		let regx = /^[\u4e00-\u9fa5]+$/ // 
		let label = ''
		if (type === 'english') {
			regx = /^[a-zA-Z]+$/
			label = ''
		} else if (type === 'engChinese') {
			regx = /^[a-zA-Z\u4e00-\u9fa5]+$/
			label = ''
		}
		if (!regx.test(str)) return {
			flag: false,
			label
		}
		return {
			flag: true
		}
	},
	/**
	 *  
	 * 1.
	 * 2.3
	 * 3.40
	 * 4.
	 * 5.
	 * @param {String} address
	 */
	isAddress: function(address) {
		if (!this.isNotEmpty(address)) return {
			flag: false,
			label: ''
		}
		// 3!
		if (address.length < 3) return {
			flag: false,
			label: '3'
		}
		// 40!
		if (address.length > 40) return {
			flag: false,
			label: '40'
		}
		// 
		var regx = /^[a-zA-Z0-9\u4e00-\u9fa5-]+$/ // 
		if (!regx.test(address)) return {
			flag: false,
			label: ''
		}
		return {
			flag: true
		}
	},
	/**
	 * 
	 * @param {Array} arr 
	 * @return {Number} 0 1 2 3 4
	 */
	pwdStrength: function(password) {
		if (!this.isNotEmpty(password)) return {
			flag: false,
			label: ''
		}
		let mode = 0
		if (password.length < 6 || password.length > 20) return {
			flag: false,
			strength: 0,
			label: ''
		} // 
		for (let i = 0; i < password.length; i++) {
			const pwdCode = password.charCodeAt(i)
			// .
			if (pwdCode >= 48 && pwdCode <= 57) { // 
				mode |= 1
			} else if (pwdCode >= 65 && pwdCode <= 90) { // 
				mode |= 2
			} else if (pwdCode >= 97 && pwdCode <= 122) { // 
				mode |= 4
			} else {
				mode |= 8
			} // 
		}

		let strength = 0
		for (let j = 0; j < 4; j++) {
			if (mode & 1) strength++
			mode >>>= 1
		}
		return {
			flag: true,
			strength
		}
	}
}
