const state = {
	sex: [{
		id: '0',
		value: ''
	}, {
		id: '1',
		value: ''
	}],
	idType: [{
		id: '0',
		value: ''
	}, {
		id: '1',
		value: ''
	}, {
		id: '',
		value: ''
	}, {
		id: '',
		value: ''
	}]

}

export default {
	name: 'dic',
	namespaced: true,
	state,
	mutations: {
		sexSet(state, payload) {
			state.sex = [{
				id: '0',
				value: ''
			}, {
				id: '1',
				value: ''
			}, {
				id: '2',
				value: ''
			}]
		}
	}
}
